<template>
    <v-btn tile :color="backgroundColor"
    :style="{
        color: textColor
    }"
    @click="jumpToLink">{{context.content}}</v-btn>
</template>

<script>
export default {
    name: "dw-button",
    props: {
        context: {
            type: Object,
        }
    },
    data() {
        return {
            backgroundColor: undefined,
            textColor: undefined,
        };
    },
    mounted() {
        this.backgroundColor = this.context.backgroundColor
            ? this.context.backgroundColor
            : this.$parent.backgroundColor;
        this.textColor = this.context.textColor
            ? this.context.textColor
            : this.$parent.textColor;
    },
    methods: {
        jumpToLink() {
            this.COMMON.routeToUrl(this.context.url);
        }
    }
}
</script>