<template>
    <div class="homepage-slides-container">
        <component v-for="(slide, i) in slides"
        :key="randomIndex + '_' + i"
        :is="'dw-' + slide.type"
        :context="slide"
        :slideUpdate="slideUpdate"
        ></component>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            slides: [],
            isLoaded: false,
            slideUpdate: 0,
            randomIndex: 0,
            primaryColor: undefined,
            secondaryColor: undefined,
            backgroundColor: undefined,
            textColor: undefined,
        }
    },
    created() {
        this.primaryColor = this.$store.state.primaryColor;
        this.secondaryColor = this.$store.state.secondaryColor;
        this.backgroundColor = this.$store.state.backgroundColor;
        this.textColor = this.$store.state.textColor;
        this.routeChanged(this.$route);
    },
    methods: {
        routeChanged(route) {
            var configUrl = this.$store.state.pageConfigDictionary[route.path];
            if(!configUrl && route.path != this.$store.state.notFoundPage) {
                this.COMMON.routeToUrl(this.$store.state.notFoundPage);
            }
            else {
                axios.get(configUrl).then(response => {
                    this.randomIndex = (this.randomIndex + 1) % 1000000;
                    this.slides = response.data;
                    this.slideUpdate = (this.slideUpdate + 1) % 2;
                }).catch(() => {
                    this.COMMON.routeToUrl(this.$store.state.notFoundPage);
                })
            }
        }
    },
    watch:{
      '$route':'routeChanged'
    },
}
</script>

<style scoped>
.homepage-slides-container {
    flex: 1;
    overflow: auto;
    position: relative;
}
</style>