<template>
    <div class="row-container"
    :class="{
        'justify-right': context.align=='right',
        'justify-center': context.align=='center'
    }">
        <div class="row-element-wrapper"
        v-for="(content, contentIndex) in context.content"
        :key="contentIndex">
        <component
        :is="'dw-' + content.type"
        :context="content"
        :primaryColor="primaryColor"
        :secondaryColor="secondaryColor"
        :BackgroundColor="backgroundColor"
        :textColor="textColor"></component>
        </div>
    </div>
</template>

<script>

export default {
    name: "dw-row",
    props: {
        context: {
            type: Object,
        }
    },
    data() {
        return {
            primaryColor: undefined,
            secondaryColor: undefined,
            backgroundColor: undefined,
            textColor: undefined,
        };
    },
    created() {
        this.primaryColor = this.context.primaryColor
            ? this.context.primaryColor
            : this.$parent.primaryColor;
        this.secondaryColor = this.context.secondaryColor
            ? this.context.secondaryColor
            : this.$parent.secondaryColor;
        this.backgroundColor = this.context.backgroundColor
            ? this.context.backgroundColor
            : this.$parent.backgroundColor;
        this.textColor = this.context.textColor
            ? this.context.textColor
            : this.$parent.textColor;
    }
};
</script>

<style scoped>
.row-container {
    width: 100%;
    display: flex;
    margin: 0 -10px;
}
.row-element-wrapper {
    margin: 0 10px;
}
</style>
