<template>
    <div class="text-container px-12 py-12"
    :class="{
        'text-container-right': context.align=='right',
        'text-container-center': context.align=='center'
    }"
    ref="text-container">
        <component
        v-for="(segment, segIndex) in segmentList"
        :key="segIndex"
        :is="'dw-' + segment.type"
        :context="segment"
        :primaryColor="primaryColor"
        :secondaryColor="secondaryColor"
        :BackgroundColor="backgroundColor"
        :textColor="textColor"></component>
    </div>
</template>

<script>
import axios from "axios";
import {DFS} from "../../js/MarkdownSolver";
// import DwPlainText from "./DwPlainText";

export default {
    name: "dw-text",
    components: {
        // DwPlainText
    },
    props: {
        context: {
            type: Object,
        },
        requireTree: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            primaryColor: undefined,
            secondaryColor: undefined,
            backgroundColor: undefined,
            textColor: undefined,

            segmentList: [],
        };
    },
    created() {
        this.primaryColor = this.context.primaryColor
            ? this.context.primaryColor
            : this.$parent.primaryColor;
        this.secondaryColor = this.context.secondaryColor
            ? this.context.secondaryColor
            : this.$parent.secondaryColor;
        this.backgroundColor = this.context.backgroundColor
            ? this.context.backgroundColor
            : this.$parent.backgroundColor;
        this.textColor = this.context.textColor
            ? this.context.textColor
            : this.$parent.textColor;
    },
    mounted() {
        if (this.context.content) {
            if (
                this.context.content.endsWith(".md") ||
                this.context.content.endsWith(".txt")
            ) {
                // external .md or .txt file
                axios.get(this.context.content).then((response) => {
                    this.solveMdString(response.data);
                    this.$nextTick(() => {
                        this.$emit("loaded");
                    });
                });
            } else {
                // a string
                this.solveMdString(this.context.content);
                this.$nextTick(() => {
                    this.$emit("loaded");
                });
            }
        }
    },
    methods: {
        solveMdString(mdString) {
            var strSegments = mdString.split("@@");
            var slotCount = 0;
            strSegments.forEach((segment) => {
                if (!segment.startsWith("slot")) {
                    // is plain text
                    this.segmentList.push({
                        type: "plain-text",
                        content: segment,
                    });
                } else {
                    // is slot
                    segment = segment.replace(/^slot/, "");
                    var slotObj = undefined;
                    try {
                        slotObj = JSON.parse(segment);
                        if(!slotObj) {
                            throw(undefined);
                        }
                    } catch {
                        if (this.context.slots && this.context.slots.length > slotCount) {
                            slotObj = this.context.slots[slotCount];
                            slotCount++;
                        }
                    }
                    if (slotObj && slotObj.type != "text" && slotObj.type != "slide") {
                        // prevent infinite recursion
                        this.segmentList.push(slotObj);
                    }
                }
            });
            this.$nextTick(() => {
                if(this.requireTree) {
                    var tree = DFS.doTree(this.$refs["text-container"]);
                    this.$emit("tree", tree);
                }
                else {
                    DFS.do(this.$refs["text-container"]);
                }
            })
        },
    }
};
</script>

<style scoped>
.text-container {
    width: 100%;
}
.text-container-center >>> .plain-text-container {
    text-align: center;
}
.text-container-right >>> .plain-text-container {
    text-align: right;
}
</style>
