<template>
    <div
        class="slide-container"
        ref="slide"
        :style="{
            'min-height': height + 'vh',
            background: backgroundColor,
        }"
    >
        <div
            class="slide-contents"
            :class="{ 'slide-contents--vertical': context.align == 'vertical' }"
        >
            <component
                v-for="(comp, compIndex) in context.content"
                :key="compIndex"
                :is="'dw-' + comp.type"
                :context="comp"
                @loaded="resetComponentHeight"
            ></component>
        </div>
        
            <v-img
                v-if="context.background"
                :src="context.background"
                lazy-src="/imgs/general/placeholder_img.png"
                height="100%"
                width="100%"
                class="background-container"
                :style="{
                    opacity: opacity,
                    background: context.background ? 'inherit' : secondaryColor,
                }"
            ></v-img>
    </div>
</template>

<script>
export default {
    name: "dw-slide",
    props: {
        context: {
            type: Object
        }
    },
    data() {
        return {
            mySlide: undefined,
            primaryColor: undefined,
            secondaryColor: undefined,
            backgroundColor: undefined,
            textColor: undefined,
            mySlideTop: 0,
            mySlideHeight: 0,
            height: 100,
        };
    },
    computed: {
        opacity() {
            if (this.mySlide) {
                let per = this.$store.state.windowScrollY - this.mySlideTop;
                per /= this.mySlideHeight;
                per = 1 - Math.max(0, Math.abs(per * 1.5) - 0.6);
                per = Math.min(1, per);
                return per;
            } else {
                return 1;
            }
        },
    },
    created() {
        if (this.context.height !== undefined) {
            this.height = this.context.height;
        }
        this.primaryColor = this.context.primaryColor
            ? this.context.primaryColor
            : this.$parent.primaryColor;
        this.secondaryColor = this.context.secondaryColor
            ? this.context.secondaryColor
            : this.$parent.secondaryColor;
        this.backgroundColor = this.context.backgroundColor
            ? this.context.backgroundColor
            : this.$parent.backgroundColor;
        this.textColor = this.context.textColor
            ? this.context.textColor
            : this.$parent.textColor;
    },
    mounted() {
        this.mySlide = this.$refs["slide"];
        this.resetComponentHeight();
    },
    methods: {
        /**
         * resets component position after interior components are loaded
         */
        resetComponentHeight() {
            if (this.mySlide) {
                this.mySlideTop = this.mySlide.offsetTop;
                this.mySlideHeight = this.mySlide.offsetHeight;
            }
        },
    }
};
</script>

<style scoped>
.slide-container {
    flex-shrink: 0;
    position: relative;
    display: flex;
    overflow: hidden;
}
.slide-contents {
    z-index: 1;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
}
.slide-contents--vertical {
    flex-direction: column;
}
.background-container {
    position: absolute;
    overflow: hidden;
    height: 100%;
    width: 100%;
}
</style>
