<script type="text/javascript">
import { router } from "../../router";

var Expression = /^([hH][tT]{2}[pP]:\/\/|[hH][tT]{2}[pP][sS]:\/\/|[fF][tT][pP]:\/\/)(.)+$/;
var objExp = new RegExp(Expression);

function routeToUrl(url) {
    if (objExp.test(url) == true) {
        // external link
        window.open(url);
    } else {
        router.push(url);
    }
}

// 暴露出这些属性和方法
export default {
    routeToUrl,
};
</script>
