import Vue from "vue";
import App from "./App.vue";
import axios from "axios";
import store from "./vuex/store";
import vuetify from "./plugins/vuetify";
import "vuetify/dist/vuetify.min.css";
import VueRouter from "vue-router";

import { initSiteConfigs } from "./js/JsonSolver";
import { router, generateRouteFromConfig } from "./router";

import global from './components/common/Common';

import DwSlide from "./components/elements/DwSlide";
import DwText from "./components/elements/DwText";
import DwButton from "./components/elements/DwButton";
import DwPlainText from "./components/elements/DwPlainText";
import DwCard from "./components/elements/DwCard";
import DwImage from "./components/elements/DwImage";
import DwRow from "./components/elements/DwRow";
import DwArticle from "./components/elements/DwArticle";


Vue.config.productionTip = false;
Vue.use(VueRouter);
Vue.prototype.COMMON = global;
Vue.component("dw-slide", DwSlide);
Vue.component("dw-text", DwText);
Vue.component("dw-button", DwButton);
Vue.component("dw-plain-text", DwPlainText);
Vue.component("dw-card", DwCard);
Vue.component("dw-image", DwImage);
Vue.component("dw-row", DwRow);
Vue.component("dw-article", DwArticle);


axios
    .get("/configs/SiteConfig.json")
    .then((response) => {
        initSiteConfigs(response.data);
        generateRouteFromConfig(response.data.defaultPages, response.data.pageConfigDictionary);
        new Vue({
            store,
            router,
            vuetify,
            render: (h) => h(App),
        }).$mount("#app");
    })
    .catch((error) => {
        console.log(error, 1);
    });

