import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
      companyName: "",
      companyLogo: "",
      primaryColor: "#ffff00",
      secondaryColor: "#ffff00",
      backgroundColor: "#ffffff",
      textColor: "#000000",
      homePage: "/home",
      notFoundPage: "/404",
      pageConfigDictionary: {},
      siteMap: [],
      footer: null,

      showHeader: true,
      windowScrollY: 0
    },
    mutations: {
      initSiteConfigs(state, val) {
        state.companyName = val.companyName;
        state.companyLogo = val.companyLogo;
        state.primaryColor = val.primaryColor;
        state.secondaryColor = val.secondaryColor;
        state.backgroundColor = val.backgroundColor;
        state.textColor = val.textColor;
        state.homePage = val.defaultPages.homePage;
        state.notFoundPage = val.defaultPages.notFoundPage;
        state.pageConfigDictionary = val.pageConfigDictionary;
        state.siteMap = val.siteMap;
        state.footer = val.footer;
      },
      toggleHeader(state, val) {
        state.showHeader = val;
      },
      windowScroll(state, scroll) {
        state.windowScrollY = scroll;
      },
    }
  })

export default store;