import Vue from "vue";
import VueRouter from "vue-router";
// import Home from '../views/Home.vue'

Vue.use(VueRouter);

var router = new VueRouter({
    mode: "history",
});

var generateRouteFromConfig = function(defaultPages, dict) {
    var parsedDict = JSON.parse(JSON.stringify(dict));
    Object.keys(parsedDict).forEach(route => {
        if (route == defaultPages.homePage) {
            // main page
            router.addRoute({
                path: "/",
                redirect: route,
            });
        } else if (route == defaultPages.notFoundPage) {
            // 404
            router.addRoute({
                path: "*",
                redirect: route,
            });
        }
        router.addRoute({
            path: route,
        });
    });
};

const originalPush = VueRouter.prototype.push;

VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch((err) => err);
};

export { router, generateRouteFromConfig };
