<template>
    <div class="plain-text-container" ref="text-container"
        :style="{
            '--primaryColor': $parent.primaryColor,
            '--secondaryColor': $parent.secondaryColor,
            '--textColor': $parent.textColor,
        }">
    </div>
</template>

<script>
import { mdSolver } from "../../js/MarkdownSolver";

export default {
    name: "dw-plain-text",
    props: {
        context: {
            type: Object,
        }
    },
    mounted() {
        if (this.context.content) {
            var dom = mdSolver(this.context.content);
            this.$refs["text-container"].appendChild(dom);
        }
    },
    methods: {}
};
</script>

<style lang="scss">
.plain-text-container {
    color: var(--textColor);
    h1,
    h2,
    h3,
    h4,
    h5 {
        color: var(--primaryColor);
        margin-top: 16pt;
        margin-bottom: 16pt;
    }
    .large {
        font-size: 2em;
    }
    blockquote {
        padding: 0 0 0 16pt;
        border-left: 5px solid var(--secondaryColor);
    }
    ::marker {
        color: var(--primaryColor);
    }
    table {
        width: 100%;
        text-align: center;
    }
    table.border {
        th {
            color: var(--primaryColor);
        }
        tbody td {
            border-top: 1px solid var(--secondaryColor);
        }
    }
}
</style>
