<template>
    <div
        class="article-container"
        :style="{
            '--primaryColor': $parent.primaryColor,
            '--secondaryColor': $parent.secondaryColor,
            '--backgroundColor': $parent.backgroundColor,
            '--textColor': $parent.textColor,
        }"
    >
        <div class="left-container elevation-4" permanent>
            <div class="left-container-i">
                <div class="left-container-top pl-4 pt-4 text-h6">目录</div>
                <v-list class="left-container-list">
                    <v-list-item
                        v-for="(node, i) in tree"
                        :key="i"
                        :class="'ml-' + node.level * 2"
                        class="tree-list-item"
                        @click="jumpTo(node.link)"
                    >
                        {{ node.name }}
                    </v-list-item>
                </v-list>
            </div>
        </div>
        <div class="right-container">
            <dw-text
                :context="context"
                :requireTree="true"
                @tree="getTree"
            ></dw-text>
        </div>
    </div>
</template>

<script>
export default {
    name: "dw-card",
    props: {
        context: {
            type: Object,
        },
    },
    data() {
        return {
            primaryColor: undefined,
            secondaryColor: undefined,
            backgroundColor: undefined,
            textColor: undefined,
            tree: [],
        };
    },
    created() {
        this.primaryColor = this.context.primaryColor
            ? this.context.primaryColor
            : this.$parent.primaryColor;
        this.secondaryColor = this.context.secondaryColor
            ? this.context.secondaryColor
            : this.$parent.secondaryColor;
        this.backgroundColor = this.context.backgroundColor
            ? this.context.backgroundColor
            : this.$parent.backgroundColor;
        this.textColor = this.context.textColor
            ? this.context.textColor
            : this.$parent.textColor;
    },
    methods: {
        getTree(tree) {
            this.tree = tree;
        },
        jumpTo(link) {
            console.log(link);
            document.getElementById(link).scrollIntoView();
        },
    },
};
</script>

<style lang="scss" scoped>
.article-container {
    flex: 1;
    height: 100%;
    width: 100%;
    display: flex;
    position: relative;
}
.left-container {
    width: 240px;
    height: 100%;
    position: absolute;
    
    top: 0;
}
.left-container-i {
    position: relative;
    height: 100%;
    top: 0;
}
.left-container-top {
    font-weight: bold;
}
.left-container-list {
    
    position: sticky;
    top: 8px;
}
.right-container {
    flex: 1;
    padding-left: 240px;
}
.tree-list-item {
    cursor: pointer;
}
</style>
