<template>
    <div class="main-layout">
        <v-hover>
            <div
                class="main-header elevation-5"
                :class="{
                    'main-header-hidden': $store.state.showHeader == false,
                    'elevation-0': $store.state.showHeader == false,
                }"
            >
                <div class="main-tb-container">
                    <div
                        class="main-tb--icon"
                        @click="COMMON.routeToUrl($store.state.homePage)"
                    >
                        <v-img
                            lazy-src="/imgs/general/placeholder.png"
                            max-height="24pt"
                            max-width="24pt"
                            :src="$store.state.companyLogo"
                        ></v-img>
                        <div class="main-tb--company-name">
                            {{ $store.state.companyName }}
                        </div>
                    </div>
                    <div class="main-tb--contents-container">
                        <v-tabs
                            :color="$store.state.primaryColor"
                            v-model="tabModel"
                        >
                            <v-menu
                                offset-y
                                rounded="0"
                                open-on-hover
                                bottom
                                v-for="(content, tabIndex) in $store.state
                                    .siteMap"
                                :key="tabIndex"
                            >
                                <template v-slot:activator="{ on }">
                                    <v-tab
                                        v-on="on"
                                        @click="
                                            contentClicked(
                                                content,
                                                null,
                                                tabIndex
                                            )
                                        "
                                    >
                                        <span class="main-tb--contents-title">{{
                                            content.text
                                        }}</span>
                                    </v-tab>
                                </template>
                                <v-list
                                    dark
                                    v-if="
                                        content.children &&
                                            content.children.length
                                    "
                                    style="padding: 0"
                                >
                                    <v-list-item
                                        v-for="(child,
                                        childIndex) in content.children"
                                        :key="childIndex"
                                        class="main-tb--contents-children"
                                        :class="{
                                            'main-tb--contents_active':
                                                tabModel == tabIndex &&
                                                childIndex ==
                                                    activeContentChildIndex,
                                        }"
                                        @click="
                                            contentClicked(
                                                content,
                                                child,
                                                tabIndex,
                                                childIndex
                                            )
                                        "
                                    >
                                        <v-list-item-title
                                            style="font-size: 10pt"
                                            >{{ child.text }}</v-list-item-title
                                        >
                                    </v-list-item>
                                </v-list>
                            </v-menu>
                        </v-tabs>
                    </div>
                </div>
            </div>
        </v-hover>
        <div class="main-body">
            <div class="content-container">
                <slot></slot>
            </div>
        </div>
        <footer class="main-footer">
            <component
                :is="'dw-' + $store.state.footer.type"
                :context="$store.state.footer"
            ></component>
        </footer>
    </div>
</template>

<script>
export default {
    name: "MainLayout",
    data() {
        return {
            windowScroll: 0,
            activeContentChildIndex: 0,
            tabModel: 0,
        };
    },
    mounted() {
        this.activeContentChildIndex = -1;
        this.tabModel = -1;
        window.addEventListener("scroll", this.windowScrolled);
        console.log(this.$route.path, this.$store.state.siteMap);
        for (
            let majorIndex = 0;
            majorIndex < this.$store.state.siteMap.length;
            majorIndex++
        ) {
            let major = this.$store.state.siteMap[majorIndex];
            console.log(major.url);
            if (major.url) {
                if (this.$route.path.startsWith(major.url)) {
                    this.tabModel = majorIndex;
                    return;
                }
            } else {
                for (
                    let childIndex = 0;
                    childIndex < major.children.length;
                    childIndex++
                ) {
                    let child = major.children[childIndex];
                    console.log("c" + child.url);
                    if (this.$route.path.startsWith(child.url)) {
                        console.log("aaa");
                        this.tabModel = majorIndex;
                        this.activeContentChildIndex = childIndex;
                        return;
                    }
                }
            }
        }
    },
    methods: {
        windowScrolled() {
            if (window.scrollY > 150 && this.windowScroll < window.scrollY) {
                this.$store.commit("toggleHeader", false);
            } else {
                this.$store.commit("toggleHeader", true);
            }
            this.$store.commit("windowScroll", window.scrollY);
            this.windowScroll = window.scrollY;
        },
        contentClicked(content, child, tabIndex, childIndex) {
            if (content.children && content.children.length > 0 && !child) {
                return;
            }
            var jumptoUrl = content.url;
            this.tabModel = tabIndex;
            if (child) {
                jumptoUrl = child.url;
                this.activeContentChildIndex = childIndex;
            }
            this.COMMON.routeToUrl(jumptoUrl);
        },
    },
};
</script>

<style scoped>
.theme--dark {
    background: #1e1e1e;
}
.main-layout {
    background: #ffffff;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}
.main-header {
    height: 36pt;
    width: 100%;
    position: fixed;
    z-index: 1000;
    top: 0pt;
    transition: 300ms;
    background: #fff;
}
.main-header-hidden {
    top: -36pt;
}
.main-tb-container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.main-tb--icon {
    margin-left: 10pt;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.main-tb--company-name {
    margin-left: 5pt;
    font-size: 12pt;
    text-anchor: middle;
    white-space: nowrap;
}
.main-tb--contents-container {
    display: flex;
    height: 100%;
    padding-right: 5pt;
}
.main-tb--contents-title {
    width: 100px;
}
.main-tb--contents-children:hover {
    background: #505050;
}
.main-tb--contents_active {
    background: #707070;
}
.main-body {
    flex: 1;
    margin-top: 36pt;
}
.content-container {
    min-height: 100%;
    /* display: flex;
    flex-direction: column; */
}
.main-footer >>> .text-container {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
}
</style>
