<template>
    <div class="card-container" :class="{'card-container-horizontal': context.align=='horizontal'}" :style="{ background: backgroundColor }">
        <component
        style="flex:1"
            v-for="(comp, compIndex) in context.content"
            :key="compIndex"
            :is="'dw-' + comp.type"
            :context="comp"
            @loaded="$emit('loaded')"
        ></component>
    </div>
</template>

<script>
export default {
    name: "dw-card",
    props: {
        context: {
            type: Object,
        },
    },
    data() {
        return {
            primaryColor: undefined,
            secondaryColor: undefined,
            backgroundColor: undefined,
            textColor: undefined,
        };
    },
    created() {
        this.primaryColor = this.context.primaryColor
            ? this.context.primaryColor
            : this.$parent.primaryColor;
        this.secondaryColor = this.context.secondaryColor
            ? this.context.secondaryColor
            : this.$parent.secondaryColor;
        this.backgroundColor = this.context.backgroundColor
            ? this.context.backgroundColor
            : this.$parent.backgroundColor;
        this.textColor = this.context.textColor
            ? this.context.textColor
            : this.$parent.textColor;
    },
    methods: {}
};
</script>

<style scoped>
.card-container {
    flex: 1;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}
.card-container-horizontal {
    flex-direction: row;
}
.card-container >>> .text-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
}
.card-container >>> .v-image__image {
    transition: 200ms;
}
.card-container:hover >>> .v-image__image {
    transform: scale(1.1);
    opacity: 0.8;
}
</style>
